











import ut from '../../utils/index.js';
export default {
	data() {
		return {
			list:[]
		}
	},
	onShow() {
		this.req_problem();
	},
	methods: {
		req_problem(){
			ut.request({
				url: "common/problem"
			}).then(data=>{
				data=data||[]
				data.forEach(item=>{
					item.check=false
				})
				this.list=data;
			})
		},
		cg_check(index){
			this.list[index].check=!this.list[index].check
		}
	}
}
